import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http'; 
import { Observable } from 'rxjs/Rx'
import { IApiError } from "./IApiError"

//ST.Web.API
import { DeviceType } from '../classes/DeviceType';
import { GlobalService } from "../../global.service";
//Remote Call
@Injectable()
export class DeviceTypesService {
    constructor(private _httpClient: HttpClient, private global: GlobalService) { }        
    
    // get: Manage/DeviceTypes      
    getDeviceTypes(): Observable<DeviceType[]> {
        var _Url = this.global.ApiConfig.apiServer + `/Manage/DeviceTypes`;
            return this._httpClient.get<DeviceType[]>(_Url)
                .catch(this.handleError);
    }
    
    // Utility
    private handleError(error: HttpErrorResponse) {
        console.error(error);
        let customError: string = "";
        if (error.error) {
            customError = error.status === 400 ? error.error : error.statusText
        }
        let apiError: IApiError = {
            customText: customError || 'Server error',
            httpError: error
        }
        return Observable.throw(apiError);
    }
}
